import tButton from '../../../components/twoButton/twoButton.vue'
import mButton from '../../../components/m-button.vue'
import {formatSeconds} from "../../../common/js/date.js"
// import wPicker from '../../../common/js/w-picker.js'
export default {
	data() {
		return {
			type1: "defult",
			type: "primary",
			con1: '取消订单',
			con2: '确认购买',
			btnValue: '确认已付款',
			closed: '关闭',
			dates: '2019-08-12 08:12',
			// rotate: false,//日志动画
			orderId: '', //订单ID
			no:'',
			order: [],
			time: '',//下单时间
			countDown:'00:00:00',//倒计时
			disabled: false,
			have:'',
			status:'',
			statusinfo:[],
			// delivery:'100',
			// discount:this.order.oil_price,//优惠
		}
	},
	onLoad(option) {
		this.orderId = option.id;
		this.status  = option.status;
		this.no = option.no;
		this.getOrderDtails();
		if(this.status == -2 || this.status == -1 || this.status == 9 || this.status == 5){
			 this.have = true
			
		} else{
			 this.have = false
		}
	},
	methods: {
		// 订单详情获取数据
		getOrderDtails() {
			this.test.post('order/query_OrderById', {
				id: this.orderId
			}).then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.order = res.data.value;
					this.status = res.data.value.status;
					this.statusinfo = this.order.latestHis
					//倒计时时间
					this.time = new Date(this.order.create_time + 8 * 3600 * 1000).toJSON().substr(0, 16).replace('T', ' ').replace(/-/g,'-')
					// 订单流程时间
					this.dates = new Date(this.statusinfo.time + 8 * 3600 * 1000).toJSON().substr(0, 16).replace('T', ' ').replace(/-/g,'-')
					const a = this.order.difference
					this.cutDown(a)
					
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 倒计时
		cutDown(time){
			const that =this;
			if(time == 0){
				clearInterval()
			}
			setInterval(function(){
				time -- ;
				that.countDown =formatSeconds(time)
			},1000)
		},
		sureBuy(num) {
			uni.redirectTo({
				url: "../invoice/invoice?id=" + this.orderId + '&number=' + num + '&no=' + this.no + '&status=' + this.status + '&company=' + this.order.org_name + '&moeny=' + this.oilPrice
			})
		},
		tell() {
			console.log(this.status)
			this.disabled = !this.disabled
			uni.showToast({
				title: "已提醒财务确认,请耐心等待",
				icon: "none"
			})
		},
		tells(){
			this.test.post('order/com_payment',{
				id:this.orderId,
			}).then(res=>{
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					// this.getOrderDtails()
					this.test.post('order/query_OrderById', {
						id: this.orderId
					}).then(res => {
						console.log(res)
						if (res.statusCode == 200 && res.data.errorCode == 0) {
							this.order = res.data.value;
							this.status = res.data.value.status;
							this.time = new Date(this.order.create_time + 8 * 3600 * 1000).toJSON().substr(0, 16).replace('T', ' ').replace(/-/g,'-')
							clearInterval();
							const a = this.order.difference
							this.cutDown(a)
							
						}
					}).catch(err => {
						console.log(err)
					})
				}
			}).catch(err=>{
				console.log(err)
			})
		},
		//取消订单
		cancelOrder() {
			const that = this;
			uni.showModal({
				"content": "确认取消订单",
				success: function(res) {
					if (res.confirm) {
						that.test.post('order/cancel_order', {
							id: that.orderId
						}).then(res => {
							console.log(res)
							if (res.statusCode == 200 && res.data.errorCode == 0) {
								uni.redirectTo({
									url:'../orderList'
								})
								uni.showToast({
									title: '取消订单成功',
								})
							} else {
								uni.showToast({
									title: '取消订单失败',
									icon: 'none'
								})
							}
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {}
				}
			})
		},
		closePage() {
			uni.navigateBack({
				delta: 1
			});
		},
		goRotate() {
			uni.navigateTo({
				url:'./orderStatus/orderStatus?id='+this.orderId +'&user_id='+this.order.user_id
			})
		}
	},
	computed: {
		oilPrice() {
			return parseFloat(this.order.oil_price*this.order.count).toFixed(2)
		},
		orderPrice(){
			return (parseFloat(this.oilPrice) + parseFloat(this.delivery)).toFixed(2)
		},
		discounts(){
			return (((this.order.market_price - this.order.oil_price)/this.order.market_price)*100).toFixed(2)
		},
		delivery(){
			return (this.order.ship_price*this.order.ship_dis).toFixed(2)
		}
	},
	components: {
		tButton,
		mButton,
		// wPicker
	}
}
